import React from 'react';
import { Link } from 'gatsby';
import classes from './Footer.module.scss';

// Images
import logo from '../../img/logo.png';
import insta from '../../img/insta.png';
import tel from '../../img/telegram.png';
import whatsapp from '../../img/whatsapp.png';

function Footer() {
	return (
		<div className={classes.footer}>
			<div className="container">
				<div className="row">
					<div className="col-lg-6 col-md-6 col-sm-12 col-12">
						<div className="d-flex justify-content-end">
							<h3 className={classes.bigtitle}>
								     
								<img className="ml-3" src={logo} alt="logo" style={{ width: '75px' }} />
							</h3>
						</div>

						<div className="d-flex justify-content-between mt-4">
							<p className={classes.adres}> : irankeshtnovin@</p>
							<p className={classes.adres}> : </p>
						</div>
						<div className="d-flex justify-content-between">
							<p className={classes.adres}> : </p>
							<p className={classes.adres}>  : </p>
						</div>
						<p className={classes.adres}> :       </p>
						<Link to="/contact">
							<div className="d-flex justify-content-center">
								<div style={{ cursor: 'pointer' }}>
									<img src={insta} alt="insta" style={{ width: '35px' }} />
								</div>
								<div style={{ cursor: 'pointer' }} className="pl-2 pr-2">
									<img src={whatsapp} alt="whatsapp" style={{ width: '35px' }} />
								</div>
								<div style={{ cursor: 'pointer' }}>
									<img src={tel} alt="telegram" style={{ width: '35px' }} />
								</div>
							</div>
						</Link>
					</div>
					<div className="col-lg-6 col-md-6 col-sm-12 col-12">
						<p className="text-right mt-3" style={{ color: 'grey' }}>
							 
						</p>
						<hr />
						<div className="d-flex flex-column justify-content-end align-items-end">
							<Link className={classes.footerlink} to="/content">
								 
							</Link>
							<Link className={classes.footerlink} to="/products">
								
							</Link>
							<Link className={classes.footerlink} to="/pests">
								 
							</Link>
							<Link className={classes.footerlink} to="/questions">
								 
							</Link>
							<Link className={classes.footerlink} to="/contact">
								  
							</Link>
							<Link className={classes.footerlink} to="/about-us">
								 
							</Link>
						</div>
					</div>
				</div>
				<hr />
				<p className={classes.copyright}>          .</p>
			</div>
		</div>
	);
}

export default Footer;
