import React, { useState, useEffect, Fragment } from 'react';
import { Link } from 'gatsby';
import { Sling as Hamburger } from 'hamburger-react';
import { Drawer } from 'antd';
import logo from '../../img/logo.png';
import classes from './Nav.module.scss';
import Links from '../Links/Links';
function Nav() {
	const [ nav, setNav ] = useState(false);
	const [ open, setOpen ] = useState(false);
	const onClose = () => {
		setOpen(false);
	};
	const handleNav = () => {
		if (window.pageYOffset > 120) {
			if (!nav) {
				setNav(true);
			}
		} else {
			if (nav) {
				setNav(false);
			}
		}
	};
	useEffect(() => {
		window.addEventListener('scroll', handleNav);
		return () => {
			window.removeEventListener('scroll', handleNav);
		};
	});
	const handleClass = () => {
		if (nav) return classes.navscrolled;
		return classes.nav;
	};
	const cls = handleClass();
	return (
		<Fragment>
			<Drawer placement={'left'} closable={true} onClose={onClose} visible={open}>
				<Links
					onClose={onClose}
					cls="flex-column-reverse justify-content-start align-items-start mt-5"
					margin="mb-3 mt-3"
				/>
			</Drawer>
			<div className={cls}>
				<div className={classes.burger}>
					<Hamburger size={20} color={'#C4C4C4'} rounded={true} toggled={open} toggle={setOpen} />
				</div>
				<div className={classes.links}>
					<Links />
				</div>
				<div className="d-flex align-items-center">
					<div>
						<Link className={classes.link} to="/">
							<p className={classes.logotext}>  </p>
						</Link>
					</div>
					<div>
						<Link className={classes.link} to="/">
							<img className={classes.logo} alt="logo" src={logo} />
						</Link>
					</div>
				</div>
			</div>
		</Fragment>
	);
}

export default Nav;
