import React from 'react';

function TopNav() {
	return (
		<div className="w-100" style={{ background: '#f2f2f2' }}>
			<p
				style={{ fontSize: '.8em', fontWeight: 'bold', letterSpacing: '1px' }}
				dir="rtl"
				className="text-center m-0 pt-3 pb-3"
			>
				    : 
			</p>
		</div>
	);
}

export default TopNav;
